import React, { Component } from "react";

function LeftsideOptions(props) {
  return (
    <React.Fragment>
      <button
        onClick={() => props.alltasksFilter(props.id)}
        className="button myday-btn"
      >
        My Day
      </button>
      <button
        onClick={() => props.importantFilter(props.id)}
        className="button important-btn"
      >
        Important
      </button>
      <button
        className="button completed-btn"
        onClick={() => {
          props.completedFilter(props.id);
        }}
      >
        Completed
      </button>
    </React.Fragment>
  );
}

export default LeftsideOptions;
