import React from "react";

function DropDownMenu() {
  return (
    <div class="dropdown-menu">
      <button
        class="menu-btn"
        onClick={() => {
          let dropdownBtn = document.querySelector(".menu-btn");
          let menuContent = document.querySelector(".menu-content");
          if (menuContent.style.display === "") {
            menuContent.style.display = "flex";
          } else {
            menuContent.style.display = "";
          }
        }}
      >
        Filter <img src="./chevron-down.svg" alt="chevron-down"></img>
      </button>
      <div class="menu-content">
        <button
          className="dropdown-myday-btn"
          onClick={() => {
            document.querySelector(".myday-btn").click();
            document.querySelector(".menu-content").style.display = "none";
          }}
        >
          My Day
        </button>
        <button
          className="dropdown-important-btn"
          onClick={() => {
            document.querySelector(".important-btn").click();
            document.querySelector(".menu-content").style.display = "none";
          }}
        >
          Important
        </button>
        <button
          className="dropdown-completed-btn"
          onClick={() => {
            document.querySelector(".completed-btn").click();
            document.querySelector(".menu-content").style.display = "none";
          }}
        >
          Completed
        </button>
      </div>
    </div>
  );
}

export default DropDownMenu;
