import React from "react";
import "./App.css";
import ToDo from "./Components/ToDo.js";
import ListofTodos from "./Components/TodoList.js";
import NewTodo from "./Components/NewTodo.js";
import LeftSideOptions from "./Components/left_side_options.js";
import NameandTime from "./Components/NameandTime.js";
import DropDownMenu from "./Components/OptionsDropDownMenu.js";

class App extends React.Component {
  constructor() {
    super();
    this.state = {
      List: [],
      originalList: [],
      //   [{
      //     type: "todo",
      //     name: "sample 1",
      //     completed: false,
      //     identity: 1,
      //     description: "this is a todo",
      //     important: false,
      //   },
      //   {
      //     type: "task",
      //     name: "sample 2",
      //     completed: false,
      //     identity: 2,
      //     description: "this is a task",
      //     important: false,
      //   },
      // ],
      IDS: [1, 2],
      completedTasks: [],
      importantList: [],
      displayingPart: "",
    };
    this.handleChange = this.handleChange.bind(this);
    this.addItem = this.addItem.bind(this);
    this.deleteItem = this.deleteItem.bind(this);
    this.makeimportant = this.makeimportant.bind(this);
    this.important_filter = this.important_filter.bind(this);
    this.alltasks_filter = this.alltasks_filter.bind(this);
    this.completedFilter = this.completedFilter.bind(this);
  }
  //-------------------------------------------------adding new todo
  componentDidMount() {
    this.alltasks_filter();
  }
  addItem() {
    console.log("new item added");
    // let id = Math.floor(Math.random() * 10);
    let newIds = this.state.IDS;
    let i = null;

    let newTask = document.querySelector(".new-task-input").value;
    let task = {};
    if (newTask === "" || newTask === undefined) {
      console.log("nothing to add");
    } else {
      for (i = 1; i <= 1000; i++) {
        if (this.state.IDS.includes(i)) {
          console.log("includes");
          continue;
        } else if (this.state.IDS.includes(i) === false) {
          console.log(i);
          newIds.push(i);
          console.log(this.state.IDS);
          break;
        }
      }
      task.type = "todo";
      task.name = newTask;
      task.completed = false;
      task.identity = i;
      let todolist = this.state.originalList.map((j) => {
        return j;
      });
      todolist.unshift(task);
      console.table(todolist);
      this.setState((prevState) => {
        return { originalList: todolist };
      });
      console.table("addItem console" + this.state.List);
    }
    // this.alltasks_filter();
    document.querySelector(".new-task-input").value = "";
    this.alltasks_filter();
  }

  //---------------------------------------------------checkbox change handler--------

  handleChange(id) {
    console.log(id);
    // let completedTask = null;
    let todolist = this.state.originalList.map((j) => {
      return j;
    });
    // console.log(todolist.length);
    for (let h = 0; h < todolist.length; h++) {
      // console.log("entered into for loop", todolist[h].identity);
      if (todolist[h].identity === id) {
        todolist[h].completed = !todolist[h].completed;
        // let completedTask = todolist[h];          // reordering part
        // todolist.splice(h, 1);
        // todolist.push(completedTask);
        // console.log("clicked one", todolist[h].completed);
        break;
      }
    }

    this.setState((prevState) => {
      return { originalList: todolist };
    });
    this.alltasks_filter();
    // this.reorderCompletedTasks();
    // console.table(this.state.List);
  }
  deleteItem(id) {
    //            there is a error check it
    // document.getElementById(id + "div").style.display = "none";
    let todolist = this.state.originalList.map((j) => {
      return j;
    });
    for (let h = 1; h <= todolist.length; h++) {
      console.log("entered into for loop", todolist[h - 1].identity);
      if (todolist[h - 1].identity === id) {
        todolist.splice(h - 1, 1);
        console.log("has to be deleted", h - 1);
        console.table(todolist);
      }
    }

    this.setState((prevState) => {
      return { originalList: todolist };
    });
    this.alltasks_filter();
    console.table(this.state.List);
  }
  completedFilter() {
    let todolist = this.state.originalList.map((j) => {
      return j;
    });
    let reorderedList = [];
    document.querySelector(".myday-btn").style.backgroundColor = "white";
    document.querySelector(".important-btn").style.backgroundColor = "white";
    document.querySelector(".completed-btn").style.backgroundColor = "grey";
    for (let h = 0; h < todolist.length; h++) {
      if (todolist[h].completed) {
        // todolist.push(todolist[h]);
        reorderedList.push(todolist[h]);
      }
    }
    // todolist.push(reorderedList);
    setTimeout(() => {
      this.setState((prev1) => {
        return { List: reorderedList, displayingPart: "Completed" };
      });
    }, 300);
  }
  makeimportant(id) {
    let todolist = this.state.originalList.map((j) => {
      return j;
    });
    // console.log(todolist.length);
    for (let h = 0; h < todolist.length; h++) {
      // console.log("entered into for loop", todolist[h].identity);
      if (todolist[h].identity === id) {
        todolist[h].important = !todolist[h].important;
        // let completedTask = todolist[h];          // reordering part
        // todolist.splice(h, 1);
        // todolist.push(completedTask);
        console.log("making this important", todolist[h].name);
        break;
      }
    }

    this.setState((prevState) => {
      return { originalList: todolist };
    });
    this.alltasks_filter();
  }

  important_filter() {
    let todolist1 = this.state.originalList.map((r) => {
      return r;
    });
    let todocompo = [];
    for (let j = 0; j < todolist1.length; j++) {
      if (todolist1[j].important) {
        todocompo.push(todolist1[j]);
      }
    }
    document.querySelector(".myday-btn").style.backgroundColor = "white";
    document.querySelector(".completed-btn").style.backgroundColor = "white";
    document.querySelector(".important-btn").style.backgroundColor = "grey";
    setTimeout(() => {
      this.setState((prev1) => {
        return { List: todocompo, displayingPart: "Important" };
      });
    }, 300);
  }

  alltasks_filter() {
    document.querySelector(".myday-btn").style.backgroundColor = "grey";
    document.querySelector(".important-btn").style.backgroundColor = "white";
    document.querySelector(".completed-btn").style.backgroundColor = "white";
    setTimeout(() => {
      this.setState((prev1) => {
        return { List: this.state.originalList, displayingPart: "My Day" };
      });
    }, 300);
  }

  render() {
    const todosComponent = this.state.List.map((toodo) => {
      return (
        <li key={toodo.identity}>
          <ToDo
            completed={toodo.completed}
            id={toodo.identity}
            text={toodo.name}
            handleChange={this.handleChange}
            deletebtn={this.deleteItem}
            important={toodo.important}
            makeimportant={this.makeimportant}
          />
        </li>
      );
    });

    return (
      <div className="App">
        <header className="header">
          <h1>Todo</h1>
        </header>

        <div className="main-part">
          <div className="options-part">
            <LeftSideOptions
              importantFilter={this.important_filter}
              alltasksFilter={this.alltasks_filter}
              completedFilter={this.completedFilter}
            />
          </div>
          <div className="todo-part">
            <div className="date-and-dropdown-div">
              <NameandTime name={this.state.displayingPart} />
              <DropDownMenu />
            </div>

            <NewTodo submit={this.addItem} />

            <div className="todo-list">
              <ul className="todo-ul-list">{todosComponent}</ul>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default App;
