import React from "react";

function ToDo(props) {
  return (
    <div id={`${props.id}div`} className="each-todo-div">
      <input
        type="checkbox"
        checked={props.completed}
        className="todo-input"
        onChange={() => props.handleChange(props.id)}
      />
      <span>{props.text}</span>
      <div className="todo-important-part">
        <input
          type="checkbox"
          value="important"
          onChange={() => props.makeimportant(props.id)}
        />
        <label>Important</label>
      </div>
      <button
        className="delete-btn"
        onClick={() => {
          props.deletebtn(props.id);
        }}
      >
        <img src="./trash.svg" alt="delete"></img>
      </button>
    </div>
  );
}

export default ToDo;
