import React, { Component } from "react";

function getDatesr() {
  let tdate = new Date();
  let dayr = [];
  dayr[0] = "Sunday";
  dayr[1] = "Monday";
  dayr[2] = "Tuesday";
  dayr[3] = "Wednesday";
  dayr[4] = "Thursday";
  dayr[5] = "Friday";
  dayr[6] = "Saturday";

  let day = dayr[tdate.getDay()];
  let months = [];
  months[0] = "January";
  months[1] = "February";
  months[2] = "March";
  months[3] = "April";
  months[4] = "May";
  months[5] = "June";
  months[6] = "July";
  months[7] = "August";
  months[8] = "September";
  months[9] = "October";
  months[10] = "November";
  months[11] = "December";
  let month = months[tdate.getMonth()];
  let date = tdate.getDate();

  return `${day}  ${month} , ${date}`;
  //   document.querySelector(".date").innerHTML = `${day}  ${month} , ${date}`;
}
function NameandTime(props) {
  return (
    <div class="name-and-time-div">
      <h3>{props.name}</h3>
      <label class="date">{getDatesr()}</label>
    </div>
  );
}

export default NameandTime;
